// 使用 Bootstrap
import './assets/lib/css/bootstrap.css'
import './assets/lib/css/date.css'
import './assets/lib/js/bootstrap'
import Vue from 'vue'
import App from './App'
import Router from 'vue-router' // 引入vue-router
import routerMap from './router' // 引入路由表
/* eslint-disable no-new */
new Vue({
  el: '#root',
  components: { App }
})
Vue.use(Router) // 声明使用vue-router
const router = new Router() // 创建路由
routerMap(router) // 路由表引入
router.start(App, '#root') // 开启应用



